@import '../../variables/variables.scss';

.App {
    background-image: linear-gradient(
        to right bottom,
        rgba($color-background, .9),
        rgba($color-turquoise-light, .9)),
    url(https://images.unsplash.com/photo-1538444520070-6f046ce1bccf?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1170&q=80);
    background-size: cover;
    background-position: left;
    background-repeat: no-repeat;
    background-attachment: fixed;
    height: 100%;
    width: 100vw;
}